
/* Feature Icons */

ul.feature-icons {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: (_size(element-margin) * 1.5) 0;
    padding-left: 0;

    &:first-child {
        margin-top: 0;
    }

    li {
        margin: (_size(element-margin) * 1.25) 0 0 0;
        padding: 0.5rem 0 0 4.5rem;
        position: relative;
        width: 50%;

        &:before {
            background-image: svg-url('<svg xmlns="http://www.w3.org/2000/svg" x="0px" y="0px" viewBox="0 0 512 512"><path d="M256,0l221.7,128v256L256,512L34.3,384V128L256,0z" fill="#{_palette(border-bg)}" /></svg>');
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
            color: _palette(fg-bold);
            display: block;
            font-size: 1.25rem;
            height: 3.25rem;
            left: 0;
            line-height: 3.25rem;
            position: absolute;
            text-align: center;
            top: 0;
            width: 3.25rem;
        }

        &:nth-child(1),
        &:nth-child(2) {
            margin-top: 0;
        }
    }

    @include breakpoint('<=small') {
        margin: 0 0 (_size(element-margin) * 1.5) 0;

        li {
            width: 100%;

            &:nth-child(2) {
                margin-top: (_size(element-margin) * 1);
            }
        }
    }
}