
/* Icon */

.icon {
    @include icon;
    border-bottom: none;
    position: relative;

    >.label {
        display: none;
    }

    &.solid {
        &:before {
            font-weight: 900;
        }
    }

    &.brands {
        &:before {
            font-family: 'Font Awesome 5 Brands';
        }
    }
}